import React, { Component } from "react";
import { Modal, ModalHeader, ModalBody } from "reactstrap";
class Slide extends Component {
    constructor(props) {
        super(props);
        this.state = {
            modal: false,
            video: "",
        };
    }

    toggle = (video) => (e) => {
        this.setState({
            modal: true,
            video: video,
        });
    };

    closeModal = () => {
        this.setState({
            modal: false,
            video: "",
        });
    };

    // componentDidMount() {
    //     $("#impactModal").on("hidden.bs.modal", function () {
    //         $("#iframeYoutube").attr("src", "#");
    //     });
    // }

    // handleClick(video) {
    //     var iframe = document.getElementById("iframeYoutube");
    //     iframe.src = "https://www.youtube.com/embed/" + video;
    //     $("#impactModal").modal("show");
    // }

    render() {
        const { data } = this.props;

        return (
            <React.Fragment>
                <div className="card rounded-0 border-0">
                    <div className="">
                        <img
                            className="img-fluid w-100 w-100 video-link"
                            alt={data.alt}
                            src={`../images/${data.image}`}
                            onClick={this.toggle(data.video)}
                        />
                    </div>
                    <div className="card-body  text-md-left text-center ">
                        <p className="maison-bold text-black font-lg">
                            {data.heading}
                        </p>
                        <p className="roboto-regular font-sm">
                            {`${data.text.substring(1, 150)}...`}
                        </p>
                    </div>
                </div>

                <Modal isOpen={this.state.modal} toggle={this.toggle} size="lg">
                    <ModalHeader toggle={this.closeModal}></ModalHeader>

                    <ModalBody>
                        <div class="embed-responsive embed-responsive-21by9">
                            <iframe
                                id="iframeYoutube"
                                className="embed-responsive-item"
                                src={`https://www.youtube.com/embed/${this.state.video}`}
                                allowFullScreen
                            />
                        </div>
                    </ModalBody>
                </Modal>
            </React.Fragment>
        );
    }
}

export default Slide;
