import React, { useState } from "react";
import {
    Carousel,
    CarouselItem,
    CarouselControl,
    CarouselIndicators,
    CarouselCaption,
} from "reactstrap";
import Slider from "react-slick";
import Button from "./button";
import Slide from "./slide";

import data from "../data.json";

const { carousel } = data;

const HomeCarousel = (props) => {
    const [activeIndex, setActiveIndex] = useState(0);
    const [animating, setAnimating] = useState(false);

    var borderStyle = {
        borderBottom: `2px solid white`,
    };

    const next = () => {
        if (animating) {
            return;
        }
        const nextIndex =
            activeIndex === carousel.length - 1 ? 0 : activeIndex + 1;
        setActiveIndex(nextIndex);
    };

    const previous = () => {
        if (animating) {
            return;
        }
        const nextIndex =
            activeIndex === 0 ? carousel.length - 1 : activeIndex - 1;
        setActiveIndex(nextIndex);
    };

    const goToIndex = (newIndex) => {
        if (animating) {
            return;
        }
        setActiveIndex(newIndex);
    };
    var settings = {
        arrows: true,
        centerMode: false,
        centerPadding: `40px`,
        dots: false,
        infinite: true,
        slidesToShow: 4,
        slidesToScroll: 1,
        responsive: [
            {
                breakpoint: 769,
                settings: {
                    dots: false,
                    centerMode: true,
                    slidesToShow: 3,
                },
            },
            {
                breakpoint: 577,
                settings: {
                    dots: true,
                    slidesToShow: 1,
                },
            },
        ],
    };
    const slides = carousel.map((data) => (
        <CarouselItem
            onExiting={() => setAnimating(true)}
            onExited={() => setAnimating(false)}
            key={data.image}
        >
            <picture>
                <source
                    className="relative-position"
                    media="(max-width: 425px)"
                    srcSet={data.responsiveImage}
                />
                <img className="img-fluid" src={data.image} alt={data.alt} />
            </picture>

            <div className="carousel-caption col-9 col-xl-6 col-md-8 text-md-left text-center mt-md-5">
                <p className="font-lg absolute-caption neosans-light text-white mb-0">
                    {data.firstLine}
                    <br /> {data.secondLine}
                    <br />
                    <a
                        className="banner-link text-white"
                        href={data.link}
                        target="_blank"
                        style={borderStyle}
                    >
                        <span className="font-lg maison-bold py-1">
                            {data.linkText}
                        </span>
                    </a>
                </p>
            </div>
        </CarouselItem>
    ));

    return (
        <section className="first-section">
            <div
                id="go-jek-slider"
                className="d-none d-md-block"
                data-ride="carousel"
            >
                <Carousel
                    activeIndex={activeIndex}
                    next={next}
                    previous={previous}
                    className="carousel-fade"
                >
                    <CarouselIndicators
                        items={carousel}
                        activeIndex={activeIndex}
                        onClickHandler={goToIndex}
                        className="homepage-carousel-indicators  mx-0 col-xl-2 col-lg-2 col-md-3 ml-lg-5 ml-xl-5 mb-xl-5 pt-lg-4 justify-content-end"
                    />
                    {slides}

                    <div className="carousel-caption py-0 carousel-btn text-uppercase">
                        <Button
                            type="outline"
                            text="VIEW OPEN POSITIONS"
                            link="/all-open-positions/"
                            position="left"
                        />
                    </div>
                </Carousel>
            </div>
            <Slider
                {...settings}
                className="row home-slider d-md-none d-block text-center"
            >
                {carousel.map((carouselItem, key) => (
                    <Slide data={carouselItem} key={key} />
                ))}
            </Slider>
        </section>
    );
};

export default HomeCarousel;
